import React from 'react';
import AddNewPost from './AddNewPost/AddNewPost';
import user from "../../../assets/Profile/usersProfileIcon.png"
import styles from "./Posts.module.css"
import {NavLink} from "react-router-dom";

function Posts(props) {
    return (<div>
        <div>
            <AddNewPost/>
        </div>
        <div>
            {props.FeedPosts.map(p => {
                return (<div className={styles.PostBox}>
                            <span>
                                <img src={p.usersPhoto != null ? p.usersPhoto : user} className={styles.UsersPhoto}/>
                            <span className={styles.DescriptionBox}>
                                <div className={styles.UsersName}>
                                    <NavLink to={`/user/${p.userId}`}>
                                        {p.fullName}
                                    </NavLink>
                                </div>
                                <div className={styles.UsersMessage}>
                                    {p.postMessage}
                                </div>
                            </span>
                            </span>
                </div>)
            })
            }
        </div>
    </div>)
}

export default Posts;